.detailsPageContainer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: calc(100vb - 115px);
    overflow: hidden;
}

.pokemonsCompareList {
    flex-shrink: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
    height: 80vb;
    overflow-y: scroll;
    padding-right: 10px;
}

.pokemonsCompareList::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background-color: transparent;
}

.pokemonsCompareList::-webkit-scrollbar {
    width: 12px;
    background-color: transparent;
}

.pokemonsCompareList::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #f4ac1c;

}

.pokemonDetails {
    height: calc(100vb - 115);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #ddd;
}

.details {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: 1240px) {


    .pokemonsCompareList {
        grid-template-columns: repeat(auto-fit, 250px);
    }
}

@media screen and (max-width: 960px) {
    .detailsPageContainer {
        flex-direction: column;
        height: auto;
        overflow: visible;
        padding-top: 100px;
    }

    .pokemonsCompareList {
        width: -webkit-fill-available;
        justify-content: center;
        height: auto;
        overflow-y: visible;
        padding-right: 0;
    }
}